<template>
  <button class="w-button" :class="[`w-button--${type}`,{
      'is-plain': plain,
      'is-round': round,
      'is-circle': circle,
      'is-disabled': disabled
  }]"
  @click="handleClick"
  :disabled='disabled'>
  <!-- 没有穿插槽时则不展示span -->
    <span v-if="$slots.default">
      <slot></slot>
    </span>
  </button>
</template>

<script>
export default {
  name: "WButton",
  props: {
    type: {
      type: String,
      default: "default"
    },
    plain: {
        type: Boolean,
        default: false
    },
    round: {
        type: Boolean,
        default: false
    },
    circle: {
        type: Boolean,
        default: false
    },
    disabled: {
        type: Boolean,
        default: false
    }
  },
  methods: {
      handleClick(e) {
        this.$emit('click',e)
      }
  }
};
</script>

